<template>
  <div class="tab">
    <div class="no-data" v-if="!pages.length">
      {{ loading ? $t('settings.loading') : $t('settings.nopromo') }}
    </div>
    <div
      :style="{
        opacity: this.loading ? 0.2 : 1,
      }"
    >
      <div
        class="promo-item row ma-2"
        v-for="(promo, i) in pages[activePage - 1]"
        :key="i"
      >
        <div class="row justify--space-between">
          <div class="flex">
            <va-icon class="mr-1" :name="'fa fa-ticket'" color="secondary" />
            <span class="promo">{{ promo.code }}</span>
            <span class="amount">{{ ` ${promo.amount} ${promo.symbol}` }}</span>
          </div>
          <div class="flex">
            <va-icon class="mr-1" :name="'fa fa-clock-o'" color="secondary" />
            {{ promo.users.length }}
          </div>
        </div>
        <div class="divider"></div>
      </div>
    </div>
    <div class="pagination_wrap" v-if="pages.length > 1">
      <va-pagination
        v-model="activePage"
        :pages="pages.length"
        visiblePages="5"
        small
      ></va-pagination>
    </div>
  </div>
</template>

<script>
import * as Settings from '@/api/Settings'

export default {
  name: 'promo-usage-state',
  data: () => ({
    loading: false,
    step: 10,
    activePage: 1,
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
    promo() {
      return this.$store.getters['promo']
    },
    pages() {
      return _.chunk(this.promo, this.step)
    },
  },
  methods: {
    loadPromoList() {
      this.loading = true
      Settings.getPromoList()
        .then(response => {
          this.loading = false
          this.$store.commit('SET_PROMO', response.data)
        })
        .catch(err => {
          this.loading = false
          if (err.response.data && err.response.data.message) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })

      this.showNext()
    },
    getPercent(val) {
      return !this.promo || !this.promo[0] || !val
        ? 0
        : (val.users.length / this.promo[0].users.length) * 100
    },
    showNext() {
      this.visibleList = this.promo.splice(0, this.step)
    },
    getRandomColor() {
      const keys = Object.keys(this.$themes)
      return this.$themes[keys[(keys.length * Math.random()) << 0]]
    },
  },
  mounted() {
    this.loadPromoList()
  }
}
</script>

<style lang="scss" scoped>
.flex {
  margin-bottom: -0.2rem;
  text-transform: uppercase;
}

.promo-item:last-child {
  .divider {
    display: none;
  }
}

.pagination_wrap {
  display: grid;
  justify-content: center;
  height: 2rem;
}

.no-data {
  display: grid;
  opacity: 0.3;
  height: 50px;
  text-align: center;
  align-items: center;
}

.promo {
  white-space: pre;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
}

.amount {
  font-size: 0.84rem;
  font-family: Consolas;
  white-space: pre;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  opacity: 0.3;
}

.divider {
  width: 100%;
  border-bottom: 1px white solid;
  opacity: 0.1;
}
</style>
