<template>
  <va-card
    class="card"
    :color="computedWidgetColor"
    :title="$t('settings.custom-deposit')"
  >
    <va-inner-loading :loading="loading">
      <div
        :style="{
          opacity: this.loading ? 0.2 : 1,
        }"
      >
        <div class="row">
          <div class="flex xs12 sm12 md12 lg12">
            <va-input v-model="amount" :label="$t('settings.amount')" />
          </div>
          <div class="flex xs12 sm12 md12 lg21">
            <va-select
              :options="['BTC', 'LTC', 'ETH']"
              v-model="currency"
              class="ma-0"
              :label="$t('settings.currency')"
              no-clear
            />
          </div>
        </div>
        <va-button class="mt-3" @click="save">{{
          $t('settings.save')
        }}</va-button>
      </div>
    </va-inner-loading>
  </va-card>
</template>

<script>
import * as Settings from '@/api/Settings'

export default {
  name: 'custom-deposit',
  inject: ['user'],
  data: () => ({
    loading: false,
    currency: 'BTC',
    amount: 0,
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
  },
  watch: {
    currency: {
      handler(v) {
        if (this.user) {
          const {
            depositMinimumBTC,
            depositMinimumLTC,
            depositMinimumETH,
          } = this.user.role.settings

          switch (v) {
            case 'BTC':
              this.amount = depositMinimumBTC || 0.01
              break
            case 'LTC':
              this.amount = depositMinimumLTC || 0.01
              break
            case 'ETH':
              this.amount = depositMinimumETH || 0.01
              break
          }
        }
      },
      immediate: true,
    },
  },
  methods: {
    save() {
      this.loading = true

      Settings.setCustomDepositMin(parseFloat(this.amount), this.currency)
        .then(res => {
          this.loading = false
          this.handleSuccess(res.data)
        })
        .catch(err => {
          this.loading = false

          if (err.response && err.response.data) {
            this.handleError(err.response.data)
          } else {
            console.log(err)
          }
        })
    },
    handleSuccess(response) {
      this.showToast(response.message, {
        icon: 'fa-check',
        position: 'bottom-center',
        duration: 4000,
        type: 'success',
      })
    },
    handleError(response) {
      this.showToast('Operation canceled. ' + response.message + '.', {
        icon: 'fa-times',
        position: 'bottom-center',
        duration: 4000,
        type: 'primary',
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.card {
  height: 100%;
  box-shadow: none;
  border-radius: 0.375rem;
}
</style>
