<template>
  <va-card
    class="card"
    :color="computedWidgetColor"
    :title="$t('settings.custom-withdraw')"
  >
    <va-inner-loading :loading="loading">
      <div
        :style="{
          opacity: this.loading ? 0.2 : 1,
        }"
      >
        <va-input
          type="textarea"
          v-model="depositErrorMessage"
          :label="$t('settings.withdraw')"
        />
        <va-input v-model="commission" label="Comission percentage" />
        <va-checkbox
          :label="$t('settings.require')"
          v-model="requireConfirmation"
          :class="requireConfirmation ? 'mb-3 mt-3' : 'mt-3'"
        />
        <va-input
          v-if="requireConfirmation"
          type="textarea"
          v-model="withdrawErrorMessage"
          :label="$t('settings.email')"
        />
        <va-button class="mt-3" @click="save">{{
          $t('settings.save')
        }}</va-button>
      </div>
    </va-inner-loading>
  </va-card>
</template>

<script>
import * as Settings from '@/api/Settings'

export default {
  name: 'custom-withdraw-error',
  inject: ['user'],
  data: () => ({
    loading: true,
    requireConfirmation: false,
    commission: 0,
    depositErrorMessage: '',
    withdrawErrorMessage: '',
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
  },
  methods: {
    updateData(user) {
      const {
        commission,
        withdrawErrorMessage,
        withdrawEmailErrorMessage,
        withdrawEmailConfirmation,
      } = user.role.settings

      this.loading = false

      this.commission = commission
      this.depositErrorMessage = withdrawErrorMessage
      this.withdrawErrorMessage = withdrawEmailErrorMessage
      this.requireConfirmation = withdrawEmailConfirmation
    },
    handleSuccess(response) {
      this.showToast(response.message, {
        icon: 'fa-check',
        position: 'bottom-center',
        duration: 4000,
        type: 'primary',
      })
    },
    handleError(response) {
      this.showToast('Operation canceled. ' + response.message + '.', {
        icon: 'fa-times',
        position: 'bottom-center',
        duration: 4000,
        type: 'primary',
      })
    },
    save() {
      this.loading = true

      const settings = this.user.role.settings
      var pending = []

      if (this.commission != settings.commission) {
        pending.push(Settings.setCommission(this.commission))
        settings.commission = this.commission
      }

      if (this.depositErrorMessage != settings.depositErrorMessage) {
        pending.push(Settings.setCustomDepositError(this.depositErrorMessage))
        settings.depositErrorMessage = this.depositErrorMessage
      }

      if (this.withdrawErrorMessage != settings.withdrawErrorMessage) {
        pending.push(Settings.setCustomWithdrawError(this.withdrawErrorMessage))
        settings.withdrawErrorMessage = this.withdrawErrorMessage
      }

      if (this.requireConfirmation != settings.withdrawEmailConfirmation) {
        pending.push(
          Settings.requireConfirmationEmail(this.requireConfirmation)
        )
        settings.withdrawEmailConfirmation = this.requireConfirmation
      }

      Promise.all(pending)
        .then(result => {
          this.loading = false

          if (!result.length) {
            this.handleSuccess({
              message: "You've successfully changed nothing!",
            })
          } else {
            this.commission = +this.commission

            settings.commission = +this.commission
            settings.depositErrorMessage = this.depositErrorMessage
            settings.withdrawErrorMessage = this.withdrawErrorMessage
            settings.withdrawEmailConfirmation = this.requireConfirmation

            result.forEach(response => {
              this.handleSuccess(response.data)
            })
          }
        })
        .catch(err => {
          this.updateData(this.user)
          this.handleError(err.response.data)
        })
    },
  },
  watch: {
    user: 'updateData',
  },

  mounted() {
    this.updateData(this.user)
  },
}
</script>

<style lang="scss">
.card {
  box-shadow: none;
  border-radius: 0.375rem;
}
</style>
