<template>
  <va-card
    class="card"
    :color="computedWidgetColor"
    :title="$t('settings.custom-terms')"
  >
    <va-inner-loading :loading="loading">
      <div
        :style="{
          opacity: this.loading ? 0.2 : 1,
        }"
      >
        <Editor
          class="terms-editor"
          v-model="terms"
          line-numbers
          :highlight="highlight"
        ></Editor>
        <va-button class="mt-3" @click="save">{{
          $t('settings.save')
        }}</va-button>
      </div>
    </va-inner-loading>
  </va-card>
</template>

<script>
import './scss/editor.style.scss'

import { PrismEditor } from 'vue-prism-editor'
import 'vue-prism-editor/dist/prismeditor.min.css'
import { highlight, languages } from 'prismjs/components/prism-core'
import 'prismjs/components/prism-clike'
import 'prismjs/components/prism-markup'
import 'prismjs/components/prism-javascript'
import 'prismjs/components/prism-css'

import * as Settings from '@/api/Settings'

export default {
  name: 'custom-terms',
  inject: ['user'],
  components: {
    Editor: PrismEditor,
  },
  data: () => ({
    loading: false,
    terms: '',
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
  },
  methods: {
    highlight(code) {
      return highlight(code, languages.html, 'html')
    },
    save() {
      this.loading = true
      Settings.setTerms(this.terms)
        .then(({ data }) => {
          this.loading = false
          this.showToast(data.message, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.loading = false
          if (err.response.data && err.response.data.message) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })
    },
  },
  watch: {},
  created() {
    this.loading = true
    Settings.getTerms().then(({ data }) => {
      this.loading = false
      this.terms = data.terms
    })
  },
}
</script>

<style lang="scss" scoped>
.prism-editor__textarea:focus {
  outline: none;
}

.card {
  margin-top: 1rem;
  box-shadow: none;
  border-radius: 0.375rem;
}

.terms-editor {
  background-color: #16162e;
  max-height: 400px;
  font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  padding: 5px 10px;
}
</style>
