<template>
  <div
    class="tab"
    :style="{
      opacity: this.loading ? 0.2 : 1,
    }"
  >
    <div class="row">
      <div class="flex xs10 sm10 md10 lg10">
        <va-input v-model="code" :label="$t('settings.code')" />
      </div>
      <div class="flex xs2 sm2 md2 lg2">
        <va-button outline color="primary" @click="genCode()">
          <va-icon name="fa fa-random" />
        </va-button>
      </div>
    </div>
    <div class="row">
      <div class="flex xs12 sm12 md12 lg6">
        <va-input v-model="amount" :label="$t('settings.amount')" />
      </div>
      <div class="flex xs12 sm12 md12 lg6">
        <va-select
          :options="['BTC', 'LTC', 'ETH']"
          v-model="currency"
          class="ma-0"
          :label="$t('settings.currency')"
          no-clear
        />
      </div>
    </div>
    <va-input
      type="textarea"
      v-model="message"
      :label="$t('settings.message')"
    />
    <va-input
      v-if="requireConfirmation"
      type="textarea"
      v-model="withdrawErrorMessage"
      :label="$t('settings.email')"
    />
    <va-button class="mt-3" @click="create">{{
      $t('settings.promocode')
    }}</va-button>
  </div>
</template>

<script>
import * as Settings from '@/api/Settings'

export default {
  name: 'create-promo-code',
  inject: ['user'],
  data: () => ({
    loading: false,
    currency: 'BTC',
    amount: 0.001,
    code: '',
    message: '',
    lastPromo: null,
  }),
  watch: {
    lastPromo(p) {
      if (!this.message) this.message = p.message
      if (!this.amount) this.amount = p.amount
    },
  },
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
    lastPromo() {
      return this.$store.getters['lastPromo']
    },
  },
  methods: {
    genCode() {
      const alpha = Array.from('1234567890qwertyuiopasdfghjklzxcvbnm')
      const len = alpha.length
      const iterationCount = 12

      let result = ''

      for (let i = 0; i < iterationCount; i++) {
        let r = Math.floor(Math.random() * (len - 0.1))

        result += alpha[r]
      }

      this.code = result.toUpperCase()
    },
    create() {
      this.loading = true

      Settings.createPromo({
        code: this.code,
        amount: parseFloat(this.amount),
        message: this.message,
        symbol: this.currency,
      })
        .then(response => {
          this.loading = false
          this.code = ''

          let msg = response.data.message
          let promo = response.data.promo

          this.showToast(msg, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })

          this.$store.commit('ADD_PROMO', promo)
        })
        .catch(err => {
          this.loading = false

          if (err.response.data && err.response.data.message) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })
    },
  },
  mounted() {
    this.$store.subscribe((mutation, state) => {
      if (mutation.type == 'SET_PROMO' && mutation.payload.length) {
        this.lastPromo = mutation.payload[0]
      }
    })
  },
}
</script>

<style lang="scss">
.tab {
  padding-top: 20px;
}
.va-select__dropdown {
  background: none;
}
</style>
