<template>
  <va-card
    :color="computedWidgetColor"
    :style="computedStyle"
    :title="$t('settings.promo.title')"
  >
    <va-tabs grow v-model="currentTab" :style="computedTabsStyle">
      <va-tab v-for="(tab, i) in tabs" :key="i">
        {{ $t('settings.promo.tabs.' + tab) }}
      </va-tab>
    </va-tabs>
    <component
      :loading="loadingTabs.includes(tabs[currentTab])"
      :class="['', '', 'pb-4'][this.currentTab]"
      :user="user"
      :is="tabs[currentTab]"
    />
  </va-card>
</template>

<script>
import CreatePromoCode from './promo-tabs/CreatePromoCode'
import PromoUsageState from './promo-tabs/PromoUsageState'

export default {
  name: 'promo',
  inject: ['user'],
  components: {
    CreatePromoCode,
    PromoUsageState
  },
  data: () => ({
    loadingTabs: [],
    tabs: ['create-promo-code', 'promo-usage-state'],
    currentTab: 0,
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    computedTabsStyle() {
      return {
        color: this.$themes.secondary,
      }
    },
  },
}
</script>

<style></style>
