<template>
  <va-card
    class="card"
    :color="computedWidgetColor"
    :title="$t('settings.trading-graph')"
  >
    <va-inner-loading :loading="loading">
      <div
        :style="{
          opacity: this.loading ? 0.2 : 1,
        }"
      >
        <div class="row">
          <div class="flex xs12 sm12 md6 lg6 ma-0">
            <va-select
              :options="['1%', '2%', '5%', '10%', '20%']"
              v-model="percent"
              class="ma-0"
              :label="$t('settings.trading.change-percent')"
              no-clear
            />
          </div>
          <div class="flex xs12 sm12 md6 lg6 ma-0">
            <va-select
              :options="currencies"
              v-model="currency"
              class="ma-0"
              :label="$t('settings.currency')"
              no-clear
            />
          </div>
          <div class="flex xs12 sm12 md12 lg12 mb-4">
            <va-input
              @input="parseDuration"
              v-model="duration"
              class="ma-0"
              :label="$t('settings.trading.change-duration')"
            />
          </div>
          </div>
        </div>
        <div class="flex">
          <div class="row">
            <va-button
              class="sm6 md6 lg6"
              color="success"
              @click="applyDirection('up')"
              ><va-icon name="fa fa-arrow-up"
            /></va-button>
            <va-button
              class="sm6 md6 lg6"
              color="danger"
              @click="applyDirection('down')"
              ><va-icon name="fa fa-arrow-down"
            /></va-button>
          </div>
        </div>
      </div>
    </va-inner-loading>
  </va-card>
</template>

<script>
import * as Trading from '@/api/Trading'

export default {
  name: 'custom-trading',
  inject: ['user'],
  data: () => ({
    loading: false,
    currencies: ['BTC', 'ETH', 'LTC', 'XRP', 'LINK', 'DOT'],
    currency: 'BTC',
    percent: '1%',
    amount: 0,
    duration: 0,
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
  },
  watch: {
    currency: {
      handler(v) {
        if (this.user) {
          const {
            depositMinimumBTC,
            depositMinimumLTC,
            depositMinimumETH,
          } = this.user.role.settings

          switch (v) {
            case 'BTC':
              this.amount = depositMinimumBTC || 0.01
              break
            case 'LTC':
              this.amount = depositMinimumLTC || 0.01
              break
            case 'ETH':
              this.amount = depositMinimumETH || 0.01
              break
          }
        }
      },
      immediate: true,
    },
  },
  methods: {
    parseDuration(e) {
      let match = e.match(/\d+/g)
      if(match) {
        this.duration = parseInt(match.join(''))
      } else {
        this.duration = 0
      }
    },
    applyDirection(direction) {
      this.loading = true

      const percent = parseInt(this.percent.replace('%', ''))

      Trading.addChangePercent({
        currency: this.currency,
        direction,
        percent: parseInt(this.percent),
        duration: this.duration,
      })
        .then(res => {
          this.loading = false
          this.handleSuccess(res.data)
        })
        .catch(err => {
          this.loading = false

          if (err.response && err.response.data) {
            this.handleError(err.response.data)
          } else {
            console.log(err)
          }
        })
    },
    handleSuccess(response) {
      this.showToast(response.message, {
        icon: 'fa-check',
        position: 'bottom-center',
        duration: 4000,
        type: 'success',
      })
    },
    handleError(response) {
      this.showToast('Operation canceled. ' + response.message + '.', {
        icon: 'fa-times',
        position: 'bottom-center',
        duration: 4000,
        type: 'primary',
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.card {
  box-shadow: none;
  border-radius: 0.375rem;
}
</style>
